html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  font-family: var(--font-family);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: var(--container-width);
  @include mobile{
  width: 100%;
  }
}

.container-second{
  width: 1010px;
  @include mobile{
    width: 100%;
    }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-title {
    font-family: Impact;
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: var(--light-color);
    margin-bottom: 54px;
    display: inline-block;
    background-color: #0F1423;
    padding: 5px 5px 8px 16px;
    position: relative;
    @include mobile{
      font-size: 30px;
    }
    &::after{
      position: absolute;
      display: block;
      width: 23px;
      top: 0;
      bottom: 0;
      left: 100%;
      content: '';
      background-image: url(../img/Subtract.svg);
      background-repeat: no-repeat;
      background-size: cover;
    }
}
.swiper-button-prev-style {
  margin: 0;
  @include mobile{
    display: none;
  }
}

.swiper-button-next-style {
  margin: 0;
  @include mobile{
    display: none;
  }
}

.overlay{
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #1F1F1F;
  opacity: 0.5;
  z-index: 5;
  display: none;
  &.overlay-active{
    display: block;
  }
}

.btn-style{
  transition: background-color .2s ease-in;
  &:hover{
    background-color: #FF5151;
  }
  &:active{
    background-color: #E32F2F;
  }
}

.btn-style-2{
  transition: all .2s ease-in;
  &:hover{
    color: #FF5151;
    border: 2px solid #FF5151;
  }
  &:active{
    color: #E32F2F;
    border: 2px solid #E32F2F;
  }
}

.modal-application {
  --bs-modal-width: 600px;
  .modal-content{
    padding: 40px 25px;
    @include mobile{
      padding: 15px 0;
    }
  }

}

.modal-title {
  margin: 0;
  @include mobile{
    font-size: 27px;
  }
}

.application {

		&__form-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
		}

		&__form {
		}

		&__form-input {
      border-bottom: 2px solid var(--accent-color, #ED3333);
      font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        padding-bottom: 16px;
        padding-left: 6px;
        padding-right: 6px;
        margin-bottom: 17px;
        outline: none;
      &::placeholder{
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
      }
		}

		&__form-sub {
      font-family: Impact;
      font-size: 22px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      text-transform: uppercase;
      margin-bottom: 25px;
      color: white;
      display: block;
      border-radius: 3px;
      background: var(--accent-color, #ED3333);
      padding: 14px 25px;
		}

		&__form-text {
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
		}

		&__form-img {
      @include mobile{
        display: none;
      }
		}
}

.modal {
}
.modal-success {
  --bs-modal-width: 370px;
		&__title {
      font-family: Impact;
      font-size: 32px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      display: block;
      text-align: center;
		}

		&__text {
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      display: block;
      text-align: center;
		}

    .modal-content {
      padding: 20px;
    }
}
