.menu{
  @include big-tablet{
  position: fixed;
  background-color: #0F1423;
  top: 0;
  left: -100%;
  width: 100%;
  max-width: 400px;
  display: flex;
  align-items: center;
  transition: left .2s ease-in;
  z-index: 7;
   flex-direction: column;
    align-items: flex-start;
    padding: 30px 20px;
  &--active{
    left: 0;
  }
  }
  @include small-tablet{
    max-width: 60%;
  }
  &__item {
  }
  &__list {

    @include big-tablet{
      flex-direction: column;
      row-gap: 25px;
      margin-bottom: 30px;
    }
  }
  .logo-link__header{
    border: none
  }
}
.burger{
  @include burger;
  display: none;
  @include big-tablet{
    order: 1;
    display: block;
    background-color: #202A4A;
    border-radius: 3px;
    margin-bottom: 15px;
    transition: background-color .2s ease-in;
    &:hover{
      background-color: #2E3E70
    }
    &:active{
      background-color: #202A4A;
    }
  }
}

.burger-close{
  @include burger;
  display: none;
  @include big-tablet{
    order: 1;
    display: block;
    background-color: transparent;
    width: 45px;
    height: 30px;
    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }

  }
}

.header {
  background-color: #0F1423;
  padding-top: 20px;
  &__navigation {
    display: flex;
    flex-wrap: wrap;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    width: 100%;
    margin-bottom: 12px;
    @include big-tablet{
      flex-wrap: wrap;
      align-items: flex-start;
    }
  }

  &__top-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    @include big-tablet{
      width: 100%;
      order: 3;
      gap: 5px;
    }
  }

  &__search {
    position: relative;
    border-bottom: 2px solid var(--dark-blue-color);
    &::after{
      display: block;
      position: absolute;
      content: '';
      top: 0;
      right: 6px;
      width: 20px;
      height: 20px;
      background-image: url(../img/search.svg);
      background-repeat: no-repeat;
      background-size: cover;
    }
    @include big-tablet{
      width: 100%;
    }
  }

  &__search-input {
    background-color: transparent;
    width: 350px;
    padding-bottom: 10px;
    padding-right: 6px;
    padding-left: 6px;
    color: var(--light-color);
    outline: none;
    &::placeholder{
      color: var(--light-color);
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
    }
    @include big-tablet{
      width: 100%;
      padding-left: 0;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 8px;
    @include big-tablet{
      order: 2;
      gap: 17px;
    }
  }

  &__contacts-link {
  }



  &__text {
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    color: var(--light-color);
  }

  &__contacts-text {
    @include big-tablet{
      font-size: 12px;
    }
  }

  &__msg {
    display: flex;
    flex-direction: column;
    gap: 10px;
    @include big-tablet{
      gap: 17px ;
    }
  }

  &__msg-link {
    position: relative;
    padding-left: 36px;
    display: block;
    &::after{
      display: block;
      position: absolute;
      content: '';
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-repeat: no-repeat;
      background-size: cover;
      width: 24px;
      height: 24px;
      @include mobile{
        width: 18px;
        height: 18px;
      }
    }
    &--wa{
      &::after{
        background-image: url(../img/whatsapp.svg);
      }
    }
    &--tg{
      &::after{
        background-image: url(../img/telegram.svg);
      }
    }
    @include big-tablet{
      font-size: 12px;
    }
    @include mobile{
      padding-left: 28px;
      font-size: 12px;
    }
  }

  &__bottom {
    width: 100%;


  }
}
