.service {

  &__content{
    margin-bottom: 50px;
  }

  &__adv-wrapper{
    &:nth-child(1){
      .service__adv{
        border: 3px solid var(--dividers, #EEF0F3);
      }
    }
    &:nth-child(2){
      .service__adv{
        border: 3px solid var(--dividers, #EEF0F3);
        border-left: none;
      }
    }
    &:nth-child(3){
      .service__adv{
        border: 3px solid var(--dividers, #EEF0F3);
      border-top: none;
      }
    }
    &:nth-child(4){
      .service__adv{
        border-right: 3px solid var(--dividers, #EEF0F3);
        border-bottom: 3px solid var(--dividers, #EEF0F3);
      }
    }
    @include small-tablet{
      .service__adv{
        border: 3px solid var(--dividers, #EEF0F3) !important;
      }
    }
  }

  &__adv {
    padding: 20px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__adv-title {
    font-family: Impact;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: 130%; /* 36.4px */
    margin-bottom: 6px;
    @include small-tablet{
     font-size: 24px;
    }
  }

  &__adv-text {
    font-family: Verdana;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 25.2px */
    margin: 0;
    @include small-tablet{
      font-size: 16px;
     }
  }

  &__link {
    font-family: Impact;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
    background-color: var(--accent-color);
    color: var(--light-color);
    margin-bottom: 75px;
    padding: 14px 25px;
    display: inline-block;
    border-radius: 3px;
    @include small-tablet{
      margin-bottom: 40px;
    }
  }

  &__text {
    font-size: 19px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 50px;
    display: block;
    @include small-tablet{
      font-size: 18px;
     }
  }
}

