table {

  border-collapse: collapse;
  border-spacing: 0;
  border-color: #EEF0F3;
  margin: 0px auto;
  width: 100%;
}

th {
  padding: 27px;
  overflow: hidden;
  text-align: left;
  color: black;
  background-color: #EEF0F3;
  font-size: 19px;
  font-style: normal;
  font-weight: 700;
  line-height: 140%;
  @include small-tablet{
    font-size: 15px;
  }
}

td {
  font-size: 14px;
  padding: 10px 5px;
  border-style: solid;
  border-width: 3px;
  overflow: hidden;
  word-break: normal;
  border-color: #EEF0F3;
  color: black;
  text-align: left;
  padding: 27px;
}

.table-wrap {
  margin: 1.5rem auto;
}

.sort-header::-moz-selection {
  background: 0 0;
}
.sort-header::selection {
  background: 0 0;
}
.sort-header {
  cursor: pointer;
}
.sort-header:after {
  content: "";
  float: right;
  margin: 7px 0.25rem 0 0.5rem;
  border-width: 0 5px 5px;
  border-style: solid;
  border-color: #fff transparent;
  opacity: 0.6;
  visibility: hidden;
}
.sort-header:hover:after {
  visibility: visible;
}
.sort-asc:after,
.sort-asc:hover:after,
.sort-desc:after {
  visibility: visible;
  opacity: 1;
}
.sort-desc:after {
  border-bottom: none;
  border-width: 5px 5px 0;
}
