.about {
  border-top: 3px solid var(--dividers, #EEF0F3);
  padding-top: 85px;
  padding-bottom: 100px;
  position: relative;
  overflow: hidden;
  @include tablet{
    padding-bottom: 0;
  }
  &::after{
    display: block;
    position: absolute;
    content: '';
    width: 618px;
    left: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    background-image: url(../img/hero-vec.png);
    background-size: cover;
    background-repeat: no-repeat;
    transform: rotate(180deg);
    z-index: -1;
    @include big-tablet{
      width: 400px;
    }
    @include tablet{
      display: none;
    }
  }

  &__swiper-wrp{
    @include mobile{
      padding-top: 64px;
      padding-bottom: 64px;
    }
    &::after{
      display: block;
      position: absolute;
      content: '';
      left: 0;
      top: 0;
      width: 60%;
      height: 100%;
      background-image: url(../img/hero-vec.png);
      transform: rotate(180deg);
      display: none;
      @include tablet{
        display: block;
      }
      @include mobile{
        left: -15px;
      }
    }
  }

  &__swiper {
    position: relative;

    .pagination-style{
      width: unset;
      position: static;
      transform: unset;
      .swiper-pagination-bullet{
        background: transparent;
        border: 2px solid black;
        opacity: 1;
        width: 12px;
        height: 12px;
      }
      .swiper-pagination-bullet-active{
        background: black;
      }

    }
  }

  &__slide {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__img {
    margin-bottom: 15px;
    @include mobile{
      max-width: 332px;
    }
  }

  &__product-name {
    display: inline-block;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    position: relative;
    margin: 0 auto;
    background-color: #0F1423;
    color: var(--light-color);
    padding: 6px 6px 6px 16px;
    max-width: 290px;
    margin-bottom: 20px;
    svg{
      position: absolute;
      display: block;
      top: 0;
      bottom: 0;
      height: 100%;
      left: 100%;
    }
  }

  &__swiper-navigation {
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: center;
  }

  &__info {
    max-width: 590px;
  }

  &__title {
    margin-bottom: 40px;
  }

  &__info-text {
    font-size: 19px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    @include mobile{
      font-size: 16px;
    }
  }
}

