.assortment {
  margin-bottom: 90px;
  border-bottom: 3px solid var(--dividers, #EEF0F3);
  @include mobile{
    border: none;
  }
  @include mobile{
    margin-bottom: 70px;
  }
  &__card-wrapper{
    &:first-child{
      .assortment__card{
        border-left: 3px solid var(--dividers, #EEF0F3);
        @include mobile{
          border-left: none;
        }
      }
    }
    &:nth-child(odd){
      @include mobile{
        border-right: 3px solid var(--dividers, #EEF0F3);
      }
    }
  }

  &__card {
    color: black;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-right: 3px solid var(--dividers, #EEF0F3);
    padding-bottom: 32px;
    @include mobile{
      border: none;
      border-bottom: 3px solid var(--dividers, #EEF0F3);

    }
    &:hover{
      .assortment__card-text{
        &::after{
          opacity: 1;
        }
      }
    }
  }

  &__card-img {
    margin-bottom: 20px;
    display: block;
    @include mobile{
      max-width: 177px;
    }
  }

  &__card-text {
    display: inline-block;
    margin: 0 auto;
    text-align: center;
    font-family: Impact;
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
    position: relative;
    &::after{
      display: block;
      content: '';
      position: absolute;
      bottom: 0;
      left: -7px;
      right: -7px;
      background-color: var(--accent-color);
      height: 13px;
      z-index: -1;
      opacity: 0;
      transition: opacity .2s ease-in;
    }
    @include mobile{
      font-size: 24px;
    }
  }
}
