.error {

  &__main-title{
    @include small-tablet{
      justify-content: center;
      align-items: center !important;
    }
  }

  &__title {
    background-color: var(--accent-color);
    &::after{
      position: absolute;
      display: block;
      width: 23px;
      top: 0;
      bottom: 0;
      left: 100%;
      content: '';
      background-image: url(../img/Subtract-error.svg);
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  &__text {
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-align: center;
    max-width: 714px;
    display: block;
    margin: 0 auto;
    margin-bottom: 40px;
  }

  &__links{
    margin-bottom: 80px;
  }

  &__link {
    font-family: Impact;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
    color: var(--light-color);
    padding: 14px 25px;
    background-color: var(--accent-color);
    border-radius: 3px;
    text-align: center;
    @include small-tablet{
      font-size: 18px;
    }
  }

  &__link-cat {
  }

  &__link-main {
    background-color: transparent;
    color: #8E8E8E;
    border: 2px solid var(--gray, #494949);
    padding: 12px 22px;
  }
}
