.universal {
  margin-bottom: 90px;
  @include small-tablet{
    margin-bottom: 40px;
  }
  &__title {
  }

  &__content{
    margin-bottom: 40px;
  }

  &__secont-title {
    font-family: Impact;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    @include mobile{
      font-size: 24px;
    }
  }

  &__list {
  }

  &__item {
    font-size: 19px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 20px;
  }

  &__item-dot{
    padding-left: 20px;
    position: relative;
    margin-bottom: 10px;
    &::after{
      display: block;
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 8px;
      height: 8px;
      background-color: var(--accent-color);
      border-radius: 100%;
    }
  }
}
