// colors
:root {
  // base
  --font-family: "Verdana", sans-serif;
  --content-width: 1190px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --accent-color: #ED3333;
  --light-color: #FFFFFF;
  --gray-color: #494949;
  --dark-color: #1F1F1F;
  --dark-gray-color: #161F38;
  --dividers-color: #EEF0F3;
  --dark-blue-color: #161F38;
}
