.tabs{
  @include tabs
}

.product {

  &__product {
  }

  &__content {
  }

  .line{
    margin: 0;
  }

  .breadcrumbs{
    @include tablet{
      display: none;
    }
  }

  &__name {
    font-family: Impact;
    font-size: 48px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 24px;
    display: block;
    @include tablet{
      font-size: 30px;
      text-align: center;
    }
  }

  &__buy {
    max-width: 400px;
    margin-bottom: 50px;
    div{
      width: 100%;
      text-align: center;
    }
    @include tablet{
      margin: 0 auto;
      max-width: 290px;
      margin-bottom: 40px;
    }
  }

  &__prise {
    font-size: 26px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 18px;

  }

  &__btn {
    font-family: Impact;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
    border-radius: 3px;
    background: var(--accent-color, #ED3333);
    padding: 15px 50px;
    color: var(--light-color);
    @include tablet{
      width: 100%;
      display: block;
      margin-bottom: 20px;
      max-width: 100%;
      width: 100%;
    }
  }

  &__garant {
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%;
    margin-bottom: 10px;
  }

  &__artic {
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
  }

  &__text {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    padding-top: 35px;
    border-top: 3px solid #EEF0F3;
  }

  &__info {
    border-right: 3px solid #EEF0F3;
    padding: 40px;
    height: 100%;
    @include small-tablet{
      border: none;
      padding: 0;
      margin-left: -15px;
      margin-right: -15px;
    }
    @include mobile{
      padding: 10px;
    }
  }

  &__info-wrapper{
    &:first-child{
      .product__info{
        border-left: 3px solid #EEF0F3;
        @include small-tablet{
          border: none;
        }
      }
    }
  }

  &__info-title {
    font-size: 19px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%;
    margin-bottom: 20px;
    @include small-tablet{
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__info-text {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 20px;
    @include mobile{
      padding: 0 15px;
    }
  }

  &__info-list {
    margin-bottom: 40px;
    @include small-tablet{
      border-bottom: 3px solid #EEF0F3;
      padding-bottom: 25px;
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  &__info-item {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 10px;
    position: relative;
    padding-left: 22px;
    &::after{
      display: block;
      content: '';
      position: absolute;
      width: 8px;
      height: 8px;
      top: 10px;
      left: 0;
      background-color: var(--accent-color);
      border-radius: 100%;
    }
  }
  &__swiper{
    max-width: 500px;
    min-height: 370px;
    @include mobile{
      max-width: 355px;
      min-height: 300px;
    }
  }
}
.container {
}
.row {
}
.col-6 {
}
.tabs {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  @include tablet{
    margin-bottom: 30px;
  }
  .swiper-button-next:after, .swiper-button-prev:after{
    color: black;
  }
  .swiper-button-next{
    right: 0;
  }
  .swiper-button-prev{
    left: 0;
  }
  &__nav {
    display: flex;
    gap: 18px;
  }

  &__nav-item {
  }

  &__nav-btn {
    width: 25px;
    height: 25px;
    border-radius: 100%;
    position: relative;
    &::after{
      display: block;
      content: '';
      position: absolute;
      width: 36px;
      height: 36px;
      border-radius: 100%;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      border: 3px solid #9A9A9A;
      opacity: 0;
    }
  }

  &__nav-btn--orange {
    background-color: #DC4C18;
  }

  &__nav-btn--gray {
    background-color: #E8E8E8;
  }

  &__nav-btn--tiff {
    background-color: #00C8F8;
  }

  &__nav-btn--violet {
    background-color: #BB285C;
  }

  &__nav-btn--blue {
    background-color: #0C59CF;
  }

  &__content {
    display: flex;

  }

  &__panel {
  }

  &__panel-img {
    max-width: 450px;
    height: 450px;
    display: block;
    margin: 0 auto;
    object-fit: contain;
    @include mobile{
      max-width: 320px;
      height: 270px;

    }
  }
}
