.breadcrumbs {
  display: flex;
  margin-bottom: 30px;
  }

  .breadcrumb-link a{
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    color: #494949;
    transition: color .2s ease-in;
    &:hover{
      color: #818181;
    }
  }

  .breadcrumbs li+li:before{
    padding:16px;
    content:">";
    color:#494949;
    background-color:transparent;
  }


  .hero-article {
    margin-bottom: 90px;
		&__text {
      font-size: 19px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;

		}

		&__list {
      padding: 0;
      padding-left: 25px;
		}

		&__item {
      font-size: 19px;
      font-style: normal;
      font-weight: 400;
      line-height: 140%;
      margin-bottom: 20px;
		}
}
.container-fluid {
}
.hero-main {

		&__img {

		}

		&__title {
      display: flex;
      flex-direction: column;
      align-items: start;
      margin-top: -30px;
      &-article{
        margin-top: -75px;
      }
		}

    &__title-text {
      margin-bottom: 20px;
		}

		&__date {
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      background-color: #0F1423;
      padding: 10px;
      color: var(--light-color);
		}
}
