.catalog {
  margin-bottom: 160px;
  &.catalog-page{
    margin-bottom: 70px;
  }
  @include mobile{
    margin-bottom: 70px;
  }
  &__title {

  }

  &__card {
  }

  &__content{
    row-gap: 90px;
    @include small-tablet{
      row-gap: 30px;
    }
  }

  &__card-img {
  }

  &__card-name {
    font-family: Impact;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    display: block;
    margin-bottom: 4px;
    @include big-tablet{
      font-size: 24px;

    }
  }

  &__card-descr {
    display: inline-block;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 15px;
    @include big-tablet{
      font-size: 15px;

    }
  }

  &__card-prise {
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    display: block;
    margin-bottom: 16px;
    @include big-tablet{
      font-size: 18px;

    }
  }

  &__card-btn {
    font-family: Impact;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--accent-color);
    padding: 14px 10px;
    width: 60%;
    border-radius: 3px;
    text-align: center;
    @include big-tablet{
      font-size: 18px;
    }
    @include small-tablet{
      font-size: 14px;
    }
    @include mobile{
      font-size: 18px;
    }
  }

  &__card-link {
    color: #8E8E8E;
    font-family: Impact;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    border-radius: 3px;
    border: 2px solid var(--gray, #494949);
    width: 40%;
    padding: 12px 10px;
    text-align: center;
    @include big-tablet{
      font-size: 18px;
    }
    @include small-tablet{
      font-size: 14px;
    }
    @include mobile{
      font-size: 18px;
    }
  }
}
