.hero {
    height: calc(100dvh - 132px);
    position: relative;
    overflow: hidden;
  &__swiper {
    height: 100%;
    position: relative;
    z-index: 2;
    .pagination-style{
      width: unset;
      position: static;
      transform: unset;
      .swiper-pagination-bullet{
        background: transparent;
        border: 2px solid white;
        opacity: 1;
        width: 12px;
        height: 12px;
      }
      .swiper-pagination-bullet-active{
        background: white;
      }
    }
    .container{
      @include mobile{
        padding: 0;
      }
    }
  }

  &__swiper-img-wrapper{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__swiper-img{
    width: 100%;
    height: 100%;
  }

  &__slide {
    height: 100%;
  }

  &__bg-1 {
    position: static;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 1400px;
    height: 100%;
  }

  &__slide-info {
    @include small-tablet{
      height: 100%;
    }
  }

  &__title {
    font-family: Impact, sans-serif;
    font-size: 52px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: var(--light-color);
    margin-bottom: 27px;
    @include tablet{
      font-size: 42px;
    }
    @include small-tablet{
      font-size: 36px;
      margin-top: 50px;
    }
    @include mobile{
      font-size: 31px;
    }
  }

  &__link {
    color: var(--light-color);
    background-color: var(--accent-color);
    border-radius: 3px;
    font-size: 21px;
    font-style: normal;
    font-weight: 800;
    line-height: normal;
    text-transform: uppercase;
    padding: 14px 44px;
    display: inline-block;
  }

  &__swiper-navigation {
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    gap: 10px;
    z-index: 2;
    @include mobile{
      bottom: 210px;
    }
  }

  &__img {
    position: relative;
    z-index: 2;
    @include tablet{
      max-width: 330px;
    }
    @include mobile{
      max-width: 250px;
    }
  }

  &__bg-2 {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    height: 100%;
    @include big-desktop{
      right: -200px;
    }
    @include tablet{
      right: -350px;
    }
    @include small-tablet{
      right: unset;
      left: 0;
      width: 100%;
      bottom: 0;
      top: unset;
      height: 200px;
    }
  }
}
.container-full{
  height: 100%;
  @include mobile{
    padding: 0 15px;
  }
}
