.footer {
  background-color: #0F1423;
  @include tablet{
    padding-top: 50px;
  }

  &__wrapper{
    width: calc(100% + 13px);
  }

  &__content {
    @include small-tablet{
      width: 100%;
    }
  }
  .logo-link{
    @include small-tablet{
      border-bottom: 3px solid var(--dark_blue, #161F38);
      display: block;
      width: 100%;
      text-align: center;
      padding-bottom: 50px;
    }
  }
  &__list {
    padding-left: 36px;
    padding-right: 36px;
    border-right: 3px solid var(--dark_blue, #161F38);
    border-left: 3px solid var(--dark_blue, #161F38);
    padding-top: 36px;
    padding-bottom: 36px;
    display: flex;
    flex-direction: column;
    gap: 16px ;
    justify-content: center;
    @include small-tablet{
      padding: 0;
      border: none;
    }
  }

  &__list-contacts{
    @include tablet{
      width: 100%;
      align-items: center;
      border-bottom: 3px solid var(--dark_blue, #161F38);
      padding-bottom: 30px;
    }
  }

  &__list-links{

    @include small-tablet{
      width: 50%;
      justify-content: center;
      align-items: start;
      padding: 26px;
      &:nth-child(2){
        border-right: 3px solid var(--dark_blue, #161F38);
      }

    }

  }

  &__item {
  }

  &__link {
  }

  &__text {
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    color: var(--light-color);
    @include mobile{
      font-size: 16px;
    }
  }
}
