.news {
  margin-bottom: 150px;
  @include mobile{
    margin-bottom: 100px;
  }
  &__title-wrapper{
    margin-bottom: 40px;
  }

  &__title {
    margin: 0;
  }

  &__all-link {
    font-family: Impact;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--accent-color);
    padding: 14px 17px 14px 25px;
    border-radius: 3px;
    text-align: center;
    @include small-tablet{
      max-width: 230px;
      margin: 0 auto;
    }
  }

  &__card {
    border: 3px solid var(--dividers, #EEF0F3);
    height: 100%;
    display: block;
    &:hover{
      .news__card-news{
        color: var(--accent-color);
      }
    }
  }

  &__card-img {
    display: block;
    width: 100%;
  }

  &__card-info{
    padding: 14px;
    border-top: 3px solid var(--dividers, #EEF0F3);
  }

  &__card-date {
    font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: normal;
color: var(--gray-color);
margin-bottom: 5px;
display: block;
  }

  &__card-news {
    font-size: 21px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    display: block;
    margin: 0;
    color: black;
    transition: color .2s ease-in;
  }
}
.center {
  text-align: center;
}

.pagination {
  display: inline-block;
}

.pagination a {
  color: #000000;
  float: left;
  padding: 2px 10px;
  text-decoration: none;
  transition: background-color .5s;
  border: 3px solid var(--dividers, #EEF0F3);
  font-size: 21px;
}

.pagination a.active {
  background-color: #0096FF;
  color: #FFFFFF;
  border: 1px solid #0096FF;
}

.pagination a:hover:not(.active) {
  background-color: #DDD;
}
